* {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

.nav ul {
  padding: 15px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.nav .title {
  font-size: 20px;
}

.nav .title span {
  font-size: 12px;
}

.nav li,
.nav a {
  list-style: none;
  text-decoration: none;
  color: #1b1b1b;
  font-weight: 600;
}

.nav a {
  border: 2px solid #ed474a;
  border-radius: 30px;
  padding: 10px;
  color: #ed474a;
}

.nav a:hover {
  background-color: #ed474a;
  color: #fff;
}

.mobileNav {
  display: none;
  background-color: #ed474a;
  width: 100%;
}

.mobileNav ul {
  list-style: none;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 10px;
  color: #fff;
}

.mobileNav .mobileMenu {
  cursor: pointer;
}

.menu {
  display: none;
  width: 100%;
}

.opened {
  display: none;
  width: 100%;
}

.opened ul {
  width: 50%;
  display: flex;
  flex-direction: column;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  flex-wrap: wrap;
  margin: 0 auto;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background-color: #ed474a;
}

.opened li,
.opened a {
  margin: 20px;
  list-style: none;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
}

.opened a:hover {
  background-color: #ed474a;
  color: #1b1b1b;
}

@media screen and (max-width: 350px) {
  .nav {
    display: none;
  }
  .mobileNav {
    display: block;
  }
  .opened {
    display: block;
  }
}
/*# sourceMappingURL=Nav.css.map */
* {
