.Works {
  margin: 100px;
  text-align: center;
  color: #102336;
}

.Works h1 {
  padding: 20px;
}

.Works .projects {
  margin: 50px 0;
  display: flex;
  flex-direction: column;
}

.Works .types {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Works .types a {
  width: 200px;
  list-style: none;
  text-decoration: none;
  color: #fff;
  font-weight: bolder;
  background-color: #ed474a;
  border-radius: 10px;
  padding: 5px 0;
  margin: 5px 0;
  cursor: pointer;
}

.Works .website {
  margin: 5% auto;
}

.Works .website img {
  position: relative;
  width: 50%;
  border-radius: 10px;
  border: 4px solid whitesmoke;
  cursor: pointer;
}

.Works .website img:hover {
  opacity: 0.7;
}

.Works #illustration {
  display: none;
  margin: 5% auto;
}

.Works #illustration img {
  position: relative;
  width: 50%;
  border-radius: 10px;
  border: 4px solid whitesmoke;
  cursor: pointer;
}

.Works #illustration img:hover {
  opacity: 0.7;
}

@media screen and (max-width: 510px) {
  .types a {
    font-size: 12px;
  }
}
